:root {  
  --color-white: #FFFFFF;
  --color-black: #111111;

  // Blue
  --color-blue-100-rgb: 198, 218, 252;
  --color-blue-100: rgb(var(--color-blue-100-rgb));
  --color-blue-300-rgb: 123, 170, 247;
  --color-blue-300: rgb(var(--color-blue-300-rgb));
  --color-blue-500-rgb: 66, 133, 244;
  --color-blue-500: rgb(var(--color-blue-500-rgb));
  --color-blue-700-rgb: 51, 103, 214;
  --color-blue-700: rgb(var(--color-blue-700-rgb));

  // Red
  --color-red-100-rgb: 244, 199, 195;
  --color-red-100: rgb(var(--color-red-100-rgb));
  --color-red-300-rgb: 230, 124, 115;
  --color-red-300: rgb(var(--color-red-300-rgb));
  --color-red-500-rgb: 219, 68, 55;
  --color-red-500: rgb(var(--color-red-500-rgb));
  --color-red-700-rgb: 197, 57, 41;
  --color-red-700: rgb(var(--color-red-700-rgb));

  // Green
  --color-green-100-rgb: 183, 225, 205;
  --color-green-100: rgb(var(--color-green-100-rgb));
  --color-green-300-rgb: 87, 187, 138;
  --color-green-300: rgb(var(--color-green-300-rgb));
  --color-green-500-rgb: 15, 157, 88;
  --color-green-500: rgb(var(--color-green-500-rgb));
  --color-green-700-rgb: 11, 128, 67;
  --color-green-700: rgb(var(--color-green-700-rgb));

  // Yellow
  --color-yellow-100-rgb: 252, 232, 178;
  --color-yellow-100: rgb(var(--color-yellow-100-rgb));
  --color-yellow-300-rgb: 247, 203, 77;
  --color-yellow-300: rgb(var(--color-yellow-300-rgb));
  --color-yellow-500-rgb: 244, 180, 0;
  --color-yellow-500: rgb(var(--color-yellow-500-rgb));
  --color-yellow-700-rgb: 240, 147, 0;
  --color-yellow-700: rgb(var(--color-yellow-700-rgb));

  // Grey
  --color-grey-100-rgb: 245, 245, 245;
  --color-grey-100: rgb(var(--color-grey-100-rgb));
  --color-grey-200-rgb: 232, 234, 237;
  --color-grey-200: rgb(var(--color-grey-200-rgb));
  --color-grey-300-rgb: 224, 224, 224;
  --color-grey-300: rgb(var(--color-grey-300-rgb));
  --color-grey-400-rgb: 189, 193, 198;
  --color-grey-400: rgb(var(--color-grey-400-rgb));
  --color-grey-500-rgb: 158, 158, 158;
  --color-grey-500: rgb(var(--color-grey-500-rgb));
  --color-grey-600-rgb: 128, 134, 139;
  --color-grey-600: rgb(var(--color-grey-600-rgb));
  --color-grey-700-rgb: 97, 97, 97;
  --color-grey-700: rgb(var(--color-grey-700-rgb));
  --color-grey-800-rgb: 60, 64, 67;
  --color-grey-800: rgb(var(--color-grey-800-rgb));
  --color-grey-900-rgb: 32, 33, 36;
  --color-grey-900: rgb(var(--color-grey-900-rgb));
  
  // Status
  --color-success: var(--color-green-500);
  --color-warning: var(--color-yellow-500);
  --color-error: var(--color-red-500);

  --color-primary: var(--color-blue-500);
  --color-secondary: var(--color-grey-800);
}